/* FilterComponent.module.css */

.filterContainer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    padding: 20px;
    border-top: 4px solid #ccc;
    max-height: 80vh;
    /* Adjust the max height as needed */
    overflow-y: auto;
    /* Add overflow-y to enable vertical scrolling */
    z-index: 1000;
    /* Ensure the overlay is above other content */
}

.filterOptions {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 10px;
}

.filterOption {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 10px;
}

.filterOptionLabel {
    font-weight: bold;
    text-transform: capitalize;
}

.checkboxContainer {
    display: grid;
    gap: 5px;
}

.checkboxLabel {
    display: flex;
    align-items: center;
}

.filterButtonContainer {
    display: flex;
    justify-content: space-between;
}

.filterButton {
    min-width: 100px;
    min-height: 34px;
}