.responsiveList {
    overflow-x: auto;
}

.list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.sectionHeader {
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 10px;
}

.listItem {
    margin-top: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
}

.itemContainer {
    display: flex;
    align-items: center;
    padding: 10px;
}

.itemImage {
    width: 100%;
    max-width: 250px;
    object-fit: cover;
    margin-right: 10px;
}

.itemTextButtonContainer {
    flex-direction: column;
}

.itemText {
    flex: 1;
    margin-right: 10px;
}

.itemText h2 {
    margin-bottom: 20px;
}

.itemText p {
    margin: 10px 0;
}

.itemTextKey {
    font-weight: bold;
    text-transform: capitalize;
}

.itemTextValue {
    text-transform: capitalize;
}

.arrow {
    display: inline-block;
    font-size: 24px;
    color: #007bff;
    /* Adjust the color as needed */
}

@media (max-width: 500px) {
    .itemContainer {
        flex-direction: column;
        align-items: center;
    }

    .itemLogo {
        width: 100%;
        height: auto;
        margin-bottom: 10px;
    }

    .itemText h2 {
        text-align: center;
    }
}

.whatsappButtonContainer {
    justify-items: end;
    display: grid;
}

.whatsappButton {
    background-color: #25d366;
    /* WhatsApp green color */
    color: #fff;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}

.whatsappButton:hover {
    background-color: #128c7e;
    /* Darker shade on hover */
}