.responsiveList {
    overflow-x: auto;
}

.sectionHeader {
    font-weight: bold;
    padding: 10px;
    background-color: #f2f2f2;
}

.list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: table;
    width: 100%;
    border-collapse: collapse;
}

.listItem {
    display: table-row;
    background-color: gainsboro;
}

.itemContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
}

.itemText {
    flex: 1;
}

.itemLogo {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.arrow {
    display: inline-block;
}

@media (max-width: 200px) {
    .itemContainer {
        display: block;
        text-align: left;
        border: none;
        border-bottom: 1px solid #ccc;
    }

    .arrow {
        margin-right: 5px;
        float: right;
    }
}