/* ProductListingPage.module.css */

.gridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    margin: 20px;
    margin-bottom: 60px;
}

.gridItem {
    border: 2px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
    height: 250px;
}

.gridItem img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    max-height: 210px;
}

.gridItem p {
    text-align: center;
    margin: 0;
    /* Remove top margin to make it cleaner */
    max-height: 20px;
    overflow: hidden;
    position: absolute;
    bottom: 10px;
    /* Align text to the bottom */
    left: 0;
    width: 100%;
}

.noResults {
    text-align: center;
    margin-top: 20px;
    font-size: 18px;
    color: #777;
}

.gridItem {
    position: relative;
}

.toggleFilter {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}

.toggleFilterButton {
    min-width: 120px;
    min-height: 34px;
}